.row {
    margin-left: 20px;
    color: #fff;

}

.posters {
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
    scroll-behavior: smooth;

}

.posters::-webkit-scrollbar {
    display: none;
}

.poster {
    cursor: pointer;
    max-height: 250px;
    margin-right: 10px;

}

.poster:hover {
    transform: scale(1.3)
}

.smallPoster {
    cursor: pointer;

    height: 200px;
    margin-right: 10px;
}

.smallPoster:hover {
    transform: scale(1.3)
}

